@import "./src/common/styles/variables";

.footerBlock {
    min-height: 20vh;
    min-width: 360px;
    border: 1px solid black;
    background-color: $primaryColor;
    text-align: center;
    .footerConteiner {
       // border: 1px solid black;
        width: 80%;
        margin: 1% auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        .iconsConteiner {
           // border: 1px solid;
          //  background-color: $primaryColor;
            width: 70%;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            list-style: none;
            padding: 0;
            margin: 0 0 20px;
            .icon {
                border: 1px solid;
                display: inline-block;
                margin: 5px 10px;
                width: 30px;
                height: 30px;
            }
        }

        .title {
            margin: 0 auto;
            letter-spacing: 3px;

            font-size: 22px;
            font-weight: 700;
            margin-bottom: 20px;
            display: block;
        }
        .endText {
            color: $primaryColor2;
        }
    }
}


